import React from 'react';

class TabComp extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
        selected: null
    };
  }

  handleChange = (index) => {
    this.setState({ selected: index });
  }

  render() {
    const { selected } = this.props;
    return (
      <div>
        <ul className="inline">
          {props.children.map((elem, index) => {
            const style = index == this.state.selected ? 'selected' : '';
            return (
              <li className={style} key={index} onClick={this.handleChange(this, index)}> 
                {elem.props.title}
              </li>
            );
          })}
        </ul>
        <div className="tab"> 
          {props.children[this.state.selected]}
        </div>
        TabComp 
        {' '}
        {selected}
      </div>
    );
  }
}

export default TabComp;
